body {}

.item {
    margin: .5rem;
    width: 350px;
    height: 250px;
    background-color: yellow;
    justify-self: center;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.item_conteiner {
    display: grid;
    height: 100%;
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
}